/*

 */
import ajax from './ajax.js'
import {
  BASE_URL,
  BLBL_ORIGIN,
  BLBL_API,
  BLBL_COMMENT,
  BLBL_M,
} from './originConf.js'
import {
  proxyResLoc,
  proxyRanking,
  proxyVideoPage,
  proxyRankingRegion,
  proxyRecommendnew,
  proxyReply,
} from './proxyApi.js'

/**
 * 
 */
export const reqPwdLogin = ({name, pwd, captcha}) => ajax(BASE_URL + '/login_pwd', {name, pwd, captcha}, 'POST')
/**
 * 
 */
export const reqSendCode = phone => ajax(BASE_URL + '/sendcode', {phone}, 'POST')
/**
 * 
 */
export const reqSmsLogin = (phone, code) => ajax(BASE_URL + '/login_sms', {phone, code}, 'POST')
/**
 * ()
 */
export const reqUserInfo = () => ajax(BASE_URL + '/userinfo')
/**
 * 
 */
export const reqLogout = () => ajax(BASE_URL + '/logout')
/**
 * 
 */
export const reqCaptcha = () => ajax(BASE_URL + '/captcha?time=' + {time:Date.now()})

/******************/
/*  BiliBili API  */
/******************/
/*  */
/**
 * 
 */
export const reqResLoc = () => proxyResLoc(BLBL_API + '/x/web-show/res/loc?jsonp=jsonp&pf=7&id=1695')
/**
 * 
 */
export const reqRanking = (rid=0) => proxyRanking(BLBL_API + '/x/web-interface/ranking?rid=' + rid + '&day=3&jsonp=jsonp')
/*  */
/**
 *  tid
 */
export const reqVideoPage = (av) => proxyVideoPage( (/^https?:\/\//.test(BLBL_M)) ? 
                                          (BLBL_M + "/video/av"+ av +".html") :
                                          (BLBL_M   + "/video/initial_state/"+av) )//(mockinitial state
/**
 * (page)
 */
export const reqRankingRegion = (tid) => proxyRankingRegion(BLBL_API + "/x/web-interface/ranking/region?rid=" + tid + "&day=7")
/**
 * 
 */
export const reqRecommendnew = (av) => proxyRecommendnew(BLBL_COMMENT + "//recommendnew," + av)
/**
 * 
 */
export const reqReply = (av) => proxyReply(BLBL_API + "/x/v2/reply?type=1&sort=2&oid=" + av + "&pn=1&nohot=1")



